<template>
  <div class="page">
    <h1>Адреса</h1>
    <!-- Добавляем табы для отображения карты и списка адресов -->
    <div>
      <button @click="activeTab = 'map'">Карта</button>
      <button @click="activeTab = 'list'">Поиск</button>
    </div>
    <div v-if="activeTab === 'map'">
      <!-- Отображаем карту с магазинами -->
      <h2>Карта</h2>
      <map-component :markers="storeL"></map-component>
    </div>
    <div v-else-if="activeTab === 'list'">
      <!-- Отображаем список адресов -->
      <h2>Поиск</h2>
      <div class="list">
        <ul>
          <li v-for="store in storeL" :key="store.id" class="bordered" @click="showOnMap(store)">
            {{ store.name }} - {{ store.address }}
          </li>
        </ul>
      </div>
    </div>
    <!-- Добавляем BottomNavigation
    <bottom-navigation></bottom-navigation>-->
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import BottomNavigation from '@/components/BottomNavigation.vue';
import MapComponent from '@/components/MapComponent.vue';
import engine from "@/assets/engine";

export default {
  components: {
    BottomNavigation,
    MapComponent,
  },
  computed: {
    //...mapGetters(['storeLocations']),
  },
  props:{
    storeLocations:{
      type: Array,
      default:[]
    },
  },
  data() {
    return {
      activeTab: 'map',
      storeL:[],
    };
  },
  methods: {
    //...mapActions(['fetchStoreLocations']),
    showOnMap(store) {
      this.activeTab = 'map';
      // Отображаем выбранный магазин на карте
      // TODO: Реализовать логику отображения на карте
    },
    setMarkets(){
      let mydata = engine.mycontents();
      //console.log(mydata)
      if(this.mydata != null) {
        this.storeL = mydata.markets;

      }else{
        this.setMarkets();
      }
    }
  },
  mounted() {
    //this.fetchStoreLocations();
    //console.log(engine.gettoken());
    //console.log(this.storeLocations)
    if(this.storeLocations != null) {
      this.storeL = this.storeLocations;
    }else{
      this.setMarkets();
    }

  },
};
</script>

<style scoped>
.page {
  padding: 20px;
}

li {
  list-style-type: none;
  margin-top: 8px;
  padding: 8px;
  font-size: 12px;
}

ul{
  padding-left: 0px;
}
</style>
