<template>
  <div v-if="msgtick > 0" class="messagebox" :class="this.message.class">
    {{ getMessage() }}
  </div>
</template>

<script>
export default {
  name: "MessageBox",
  props: ['message'],
  data(){
    return{
      myclass: 'success',
      mess: 'Ничего не написано!',
      msgtick: 3,
    }
  },
  mounted() {
    this.tick();
  },
  methods:{
    getMessage(){
      if(this.message.message == undefined){
        return this.message.message;
      }
      return this.mess;
    },
    tick(){
      //console.log(this.msgtick)
      if(this.msgtick > 0){
        this.msgtick --;
      }else{
        this.msgtick = 0;
      }
      setTimeout(()=>{ this.tick(); }, 1000);
    }
  }
}
</script>

<style scoped>

.messagebox{
  width:84%;
  min-height: 48px;
  text-align: center;
  border-radius: 8px;
  margin: auto;
  padding: 16px;
}

.error{
  background: #995555;
  border: 1px solid #553333;
  color: #553333;
}

.success{
  background: #559955;
  border: 1px solid #335533;
  color: #335533;
}

</style>