          <td><input type="text" class="bordered w100" style="margin-top: 16px;" placeholder="Имя" v-model="user.card"></td>
        </tr>
        <tr>
          <td>Почта</td>
          <td><input type="text" class="bordered w100" style="margin-top: 16px;" placeholder="Имя" v-model="user.email"></td>
        </tr>
        <tr>
          <td>Потрачено</td>
          <td><input type="text" class="bordered w100" style="margin-top: 16px;" placeholder=""></td>
        </tr>
        <tr>
          <td>Остаток</td>
          <td><input type="text" class="bordered w100" style="margin-top: 16px;" placeholder=""></td>
        </tr>
      </table>
      <!--<p>Имя: {{ use.fullName }}</p>
      <p>Телефон: {{ use.phoneNumber }}</p>
      <p>Карта: {{ use.cardNumber }}</p>
      <p>Email: {{ use.email }}</p>
      <p>Потраченных: {{ use.spentAmount }}</p>
      <p>Остаток: {{ use.remainingAmount }}</p>-->
