  background-color: #333;
  color: #fff;
  height: 50px;
  display: flex;
  align-items: center;
  padding: 0 20px;
}

.logo img {
  height: 40px;
}

.loyalty-points {
  margin-left: auto;
  display: flex;
  align-items: center;
}

