<template>
    <div class="swipper" id="swipper">
      <div class="draggable"></div>
    </div>
</template>

<script>
export default {
  name: 'swipper',
  props:['actions'],
  data() {
    return {
      isDone: false,
      scrollXX: 0,
    };
  },
  mounted() {
    //console.log(this.actions);
    //let swpr = document.querySelector('#swipper');
    let element = document.getElementById('swipper');
    let maxScroll = element.scrollWidth - element.clientWidth - 32;
    let s = this;
    element.addEventListener('scroll', function(event) {
      // Обработка события скролла
      let scrollPosition = element.scrollLeft;
      s.scrollXX = scrollPosition + " : "+maxScroll;
      if( scrollPosition >= maxScroll){
        if(!s.isDone) {
          s.isDone = true
          if(s.actions != undefined){
            if(s.actions() == false){
              alert('Ошибка авторизации!');
              element.scrollLeft = 0;
              s.isDone = false;
            }
          }
        }
      }
    });
  },
  methods: {

  },
};
</script>

<style>
.swipper {
  margin: auto;
  margin-top: 64px;
  width: calc(100vw - 196px);
  overflow-x: auto;
  white-space: nowrap;
  scrollbar-width: thin; /* Ширина скроллбара */
  scrollbar-color: #888888 #dddddd; /* Цвет скроллбара */
}

/* Стили для скроллбара (работают в Chrome и Firefox) */
.swipper::-webkit-scrollbar {
  width: 32px; /* Ширина скроллбара */
  height: 36px;
}

.swipper::-webkit-scrollbar-track {
  background-color: #eee; /* Цвет фона трека */
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.03);
  height: 3px;
  border-radius: 64px;
  border: 1px solid #DDDDDD;
}

.swipper::-webkit-scrollbar-thumb {
  background-color: #559955; /* Цвет ползунка */
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.03);
  border-radius: 32px; /* Радиус скругления ползунка */
}

.swipper::-webkit-scrollbar-thumb:hover {
  background-color: #559955; /* Цвет ползунка при наведении */
}

.draggable {
  height: 2px;
  width:1300px;
}
</style>
